<template>
	<div class="groupThree">
		<van-field v-model="formData.zhoushu" label="周数" @click-input="showZhoushuList=true" placeholder="请选择周数" required
		 readonly />
		<div class="van-cell van-field van-cell--required">
			<div class="van-cell__title van-field__label">
				<span>视频课</span>
			</div>
			<div style="position: relative;">
				<van-radio-group v-model="formData.isSee" direction="horizontal">
					<van-radio :name="1">已看完</van-radio>
					<van-radio :name="2">未看完</van-radio>
				</van-radio-group>
			</div>
		</div>
		<van-field v-model="formData.fenshu" label="考试分数" type="number" clearable required placeholder="请输入0-100的分数" />
		<div class="van-cell van-field van-cell--required">
			<div class="van-cell__title van-field__label">
				<span>实践课作业</span>
			</div>
			<div style="position: relative;">
				<van-radio-group v-model="formData.submit_word" direction="horizontal">
					<van-radio :name="1">已提交</van-radio>
					<van-radio :name="2">未提交</van-radio>
				</van-radio-group>
			</div>
		</div>
		<van-field v-model="formData.wenti" autosize label="遇到的问题" type="textarea" placeholder="请输入问题" maxlength="100"
		 show-word-limit />
		<div style="width: 100%;height: 1px; background-color: rgba(0,0,0,0.05);"></div>
		<van-button type="info" @click="submit" class="btn">提交</van-button>
		<van-popup v-model="showZhoushuList" position="bottom" @close="closeArea">
			<ul>
				<li style="height: 50px;line-height: 50px;" v-for="(item,index) in zhoushu" :key="index" @click="choseZhoushu(`第${item.week_no}周`,item.id)">{{`第${item.week_no}周`}}</li>
			</ul>
		</van-popup>
		<van-number-keyboard safe-area-inset-bottom />
	</div>
</template>

<script>
	import { week_score_submit } from '../api/newForm.js'
	export default {
		props: ['zhoushu', 'member_id','zuhao'],
		data() {
			return {
				formData: {
					isSee: 1,
					zhoushu: '',
					wenti: '',
					fenshu: '',
					zhoushuCode:'',
					submit_word:1
				},
				showZhoushuList: false,
				group_no:''
			}
		},
		mounted() {
			if(this.$route.query.group_no){
				this.group_no = this.$route.query.group_no
			}else{
				this.$toast('不合法')
			}
		},
		methods: {
			async submit() {
				var list = JSON.parse(JSON.stringify(this.formData))
				if (list.zhoushu && list.fenshu) {
					if (this.checkFenshu(list.fenshu)) {
						var data = {
							member_id: this.member_id,
							week_no: list.zhoushuCode,
							score:list.fenshu,
							is_view_video:list.isSee,
							problem:list.wenti,
							homework:list.submit_word
						}
						this.$emit('startLoading')
						var result = await week_score_submit(data,this.zuhao,this.group_no)
						if(result.data.code=="00000"){
							this.$router.replace('/success')
						}
					} else {
						this.$toast('分数范围区间为0~100');
					}
				} else {
					this.$toast('请填写完整数据');

				}
			},
			closeArea() {
				this.showZhoushuList = false
			},
			choseZhoushu(item,id) {
				this.formData.zhoushu = item
				this.formData.zhoushuCode = id
				this.closeArea()
			},
			checkFenshu(value) {
				if (value >= 0 && value <= 100) {
					return true
				} else {
					return false
				}
			}
		}
	}
</script>

<style>
	.groupThree .van-field__label {
		width: 6.8em !important;
	}
</style>
<style scoped>
	.btn {
		margin-top: 30px;
		width: 100px;
		height: 40px;
		background-color: #1989FA !important;
		border: none !important;
		
	}
</style>
