<template>
	<div class="groupTwo">
		<van-field v-model="formData.zuhao" label="组号" readonly />
		<van-field v-model="formData.zuming" label="组名" readonly />
		<van-field v-model="formData.zuzhang" label="组长" readonly />
		<van-field v-model="formData.fuzuzhang" label="副组长" readonly />
		<van-field v-model="formData.xuexiweiyuan" label="学习委员" readonly />
		<van-field v-model="formData.mobile" label="手机号" required maxlength="11" v-show="yanzheng==false" type="number"
		 placeholder="请输入手机号" clearable />
		<van-button type="info" @click="submit" class="btn" v-show="yanzheng==false">验证</van-button>
		<groupThree v-if="yanzheng==true" @startLoading="loading=true" :zhoushu='selectData.week_list' :zuhao='this.formData.zuhao'
		 :member_id='selectData.member_id'></groupThree>
		<van-number-keyboard safe-area-inset-bottom />
		<div v-show="loading">
			<!-- <van-loading color="#1989fa" style="position: fixed; top: 50%;left: 50%;transform: translate(-50%,-50%);z-index: 10000;" /> -->
			<van-overlay :show="true" />
		</div>
	</div>
</template>

<script>
	import {
		getSelectData,
		week_score_submit,
		checkPhone
	} from '../api/newForm.js'
	import groupThree from './groupThree.vue'
	export default {
		components: {
			groupThree
		},
		async mounted() {
			if (this.$route.query.group_no) {
				this.group_no = this.$route.query.group_no
				document.title = '信息采集'
				// console.log(this.$toast)
				this.loading = true
				var select = await getSelectData(this.group_no)
				if (select.data.code == "00000") {
					var data = select.data.data.group_info
					this.loading = false
					this.formData.zuzhang = data.first_leader
					this.formData.fuzuzhang = data.second_leader
					this.formData.xuexiweiyuan = data.study_leader
					this.formData.zuming = data.name
					this.formData.zuhao = data.id
				} else {
					this.$toast(select.data.msg)
				}
			} else {
				this.$router.push('/error')
			}

		},
		data() {
			return {
				loading: false,
				formData: {
					zuhao: '',
					zuming: '',
					zuzhang: '',
					fuzuzhang: '',
					xuexiweiyuan: '',
					mobile: '',
				},
				yanzheng: false,
				selectData: [],
				group_no: ''
			}
		},
		methods: {
			async submit() {
				if (this.formData.mobile != '') {
					if (this.checkMobile(this.formData.mobile)) {
						// this.$router.push('/groupThree')
						var data = {
							mobile: this.formData.mobile
						}
						this.loading = true
						var select = await checkPhone(data, this.formData.zuhao, this.group_no)
						if (select.data.code == "00000") {
							this.yanzheng = true
							this.loading = false
							this.selectData = select.data.data
						} else if (select.data.code == "80001") {

							// const beforeClose = (action) => new Promise((resolve) => {
							// 	if (action === 'confirm') {
							// 		this.$router.replace('/group?group_no=' + this.group_no)
							// 		resolve(true);
							// 	} else {
							// 		// 拦截取消操作
							// 		this.loading=false
							// 		resolve(false);
									
							// 	}
							// });

							// this.$dialog.confirm({
							// 	title: '错误',
							// 	message: select.data.msg,
							// 	confirmButtonText:'立即注册',
							// 	beforeClose
							// });
							this.$dialog.confirm({
							  title: '错误',
							  confirmButtonText:'立即注册',
							  message: select.data.msg,
							})
							  .then(() => {
							    // on confirm
								this.$router.replace('/group?group_no=' + this.group_no)
							  })
							  .catch(() => {
							    // on cancel
								this.loading=false
							  });
						} else {
							this.$toast(select.data.msg)
						}
					} else {
						this.$toast('手机号格式不正确');
					}
				} else {
					this.$toast('手机号不能为空');
				}
			},
			checkMobile(value) {
				if (value != '') {
					const reg = /^1[3|4|5|6|7|8|9]\d{9}$/
					if (reg.test(value)) {
						return true
					} else {
						return false
					}
				} else {
					return true
				}
			}
		}
	}
</script>

<style>
	.groupTwo .van-field__label {
		width: 6.8em !important;
	}
	
	.van-dialog{
		z-index: 3000 !important;
	}
</style>
<style scoped>
	.btn {
		margin-top: 30px;
		width: 100px;
		height: 40px;
		background-color: #1989FA !important;
		border: none !important;
	}
</style>
