import request from '../unit/request.js'
import axios from 'axios'
const query = 'group_no=e4da3b7fbbce2345d7772b0674a318d5'
const URL = process.env.VUE_APP_BASE_URL
export function getSelect(group_no){
	return request({
		url:`${URL}/api/member_submit?group_no=${group_no}`,
		method:'get'
	})
}

export function member_submit(data,group_no){
	return request({
		url:`${URL}/api/member_submit?group_no=${group_no}`,
		method:'post',
		data
	})
}

export function getSelectData(group_no){
	return request({
		url:`${URL}/api/week_score_submit?group_no=${group_no}`,
		method:'get',
	})
}

export function week_score_submit(data,id,group_no){
	return request({
		url:`${URL}/api/week_score_submit?id=${id}&group_no=${group_no}`,
		method:'post',
		data
	})
}

export function checkPhone(data,id,group_no){
	return request({
		url:`${URL}/api/check_mobile?group_no=${group_no}&id=${id}`,
		method:'post',
		data
	})
}